import { styled } from "@mui/joy/styles";
import Button from "@mui/joy/Button";
import Grid from "@mui/joy/Grid";
import Box from "@mui/joy/Box";
import ListItemDecorator from "@mui/joy/ListItemDecorator";
import React from "react";
import Select from "@mui/joy/Select";
import Option from "@mui/joy/Option";
import { BaseImage } from "./utils";

const BigButton = styled(Button)(({ theme }) => ({
  fontSize: theme.vars.fontSize.xl,
  backgroundColor: "rgb(82, 15, 123)",
  "&:hover": {
    backgroundColor: "#881acb",
  },
}));

const BaseItem = ({
  mainImg,
  mainTitle,
  firstButton,
  firstLink,
  secondButton,
  secondLink,
  children,
}) => (
  <Grid item xs={12} lg={4}>
    <BaseImage src={mainImg} alt="main" />
    <h3>{mainTitle}</h3>
    {children}
    <Grid container spacing={2} alignItems="center" justifyContent="center">
      <Grid item>
        <BigButton onClick={() => window.open(firstLink, "_blank")}>
          {firstButton}
        </BigButton>
      </Grid>
      <Grid item>
        {secondButton != null && (
          <BigButton onClick={() => window.open(secondLink, "_blank")}>
            {secondButton}
          </BigButton>
        )}
      </Grid>
    </Grid>
  </Grid>
);

const LangFlag = ({ langCode, langValue }) => (
  <>
    <ListItemDecorator>
      <Box mr={1}>
        <img src={`https://flagcdn.com/w20/${langCode}.png`} alt="1" />
      </Box>
    </ListItemDecorator>
    {langValue}
  </>
);

const TitleHeader = styled("h1")(({ isMargin }) => ({
  marginBlockStart: isMargin === true ? "100px" : undefined,
  fontStyle: "italic",
}));

const Initial = ({ langValue, putLang }) => (
  <>
    <BaseImage
      src="https://user-images.githubusercontent.com/33064544/199813309-6a7263e5-0189-4ebf-816c-d8aadbdb8702.png"
      alt="logo"
    />
    <img
      src="https://user-images.githubusercontent.com/33064544/199811730-6d733409-519b-49b0-bd41-7df7ecd60f3b.png"
      alt="landing"
      style={{ maxWidth: "100%" }}
    />
    <TitleHeader>Roo's on DLive/Trovo</TitleHeader>
    <Grid container spacing={2} alignItems="center" justifyContent="center">
      <Grid item>
        <img
          src="https://user-images.githubusercontent.com/33064544/199810835-cb63021a-5822-46d3-a066-9e7e2a21f05c.png"
          alt="coin"
        />
      </Grid>
      <Grid item xs={12} md={6}>
        <h2>Find Roo on DLive/Trovo for some balance</h2>
      </Grid>
      <Grid item>
        <img
          src="https://user-images.githubusercontent.com/33064544/199810835-cb63021a-5822-46d3-a066-9e7e2a21f05c.png"
          alt="coin"
        />
      </Grid>
    </Grid>
    <Grid container spacing={2} alignItems="center" justifyContent="center">
      <Grid item>
        DLive/Trovo lovers, fans, and streamers - are you ready?
        <Box sx={{ color: "yellow" }}>
          Mr. Roo is hopping around DLive/Trovo all month!
        </Box>
        Lure him to your stream for subs, bits, codes, and cash!
      </Grid>
      <Grid item>
        <Grid container spacing={2} alignItems="center" justifyContent="center">
          <Grid item>
            <BigButton
              onClick={() =>
                window.open("https://dlive.tv/s/browse/57608/Slots", "_blank")
              }
            >
              See who's live on DLive
            </BigButton>
          </Grid>
          <Grid item>
            <BigButton
              onClick={() =>
                window.open("https://trovo.live/games/Casino", "_blank")
              }
            >
              See who's live on Trovo
            </BigButton>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
    <TitleHeader isMargin>Help Roo find your stream</TitleHeader>
    <Grid container spacing={2} alignItems="center" justifyContent="center">
      <Grid item>
        Follow these steps to get Roo to join your chat. (or to find chats to
        join yourself...)
      </Grid>
      <Grid item>
        <Grid container spacing={2}>
          <BaseItem
            mainImg="https://user-images.githubusercontent.com/33064544/199810950-ce949a98-0451-4829-a3fc-a9fcd05b92de.jpeg"
            mainTitle="Go live on DLive/Trovo"
            firstButton="Go to DLive"
            firstLink="https://dlive.tv/"
            secondButton="Go to Trovo"
            secondLink="https://trovo.live"
          >
            Stream yourself playing any of your favorite Roobet games.
          </BaseItem>
          <BaseItem
            mainImg="https://user-images.githubusercontent.com/33064544/199810950-ce949a98-0451-4829-a3fc-a9fcd05b92de.jpeg"
            mainTitle="Add !RooTV to your title"
            firstButton="Live on DLive"
            firstLink="https://dlive.tv/s/browse/57608/Slots"
            secondButton="Live on Trovo"
            secondLink="https://trovo.live/games/Casino"
          >
            Add 'RooTV' to your stream title so Roo can easily find you when
            searching DLive/Trovo.
          </BaseItem>
          <BaseItem
            mainImg="https://user-images.githubusercontent.com/33064544/199811061-6d9eef1f-274c-43a9-8e89-f69e9a8188e5.jpg"
            mainTitle="Tweet at us"
            firstButton="Tweet to Roo"
            firstLink="https://twitter.com/Roobet"
          >
            Tweet a link to your stream at Roo using #Roobet and tag @Roobet so
            he can easily find you.
          </BaseItem>
        </Grid>
      </Grid>
    </Grid>
    <TitleHeader isMargin>RoobetLIVE Party</TitleHeader>
    <Grid container spacing={2} alignItems="center" justifyContent="center">
      <Grid item>
        <img
          src="https://user-images.githubusercontent.com/33064544/199811213-dfa65624-7981-4a13-9cc4-5c74271e04ea.png"
          alt="left"
        />
      </Grid>
      <Grid item md={6}>
        <Grid container spacing={2} alignItems="center" direction="column">
          <Grid item>
            Join our DLive channel for your chance to win a piece of the $10000
            prize pool!
          </Grid>
          <Grid item>
            <BigButton
              onClick={() => window.open("https://dlive.tv/Roobet", "_blank")}
            >
              DLive Channel
            </BigButton>
          </Grid>
          <Grid item>Next Live Party: November 10th @ 7pm GMT / / 3pm ET</Grid>
        </Grid>
      </Grid>
      <Grid item>
        <img
          src="https://user-images.githubusercontent.com/33064544/199811289-cfb729c6-6ba6-42b3-9089-cd42f80eb35c.png"
          alt="right"
        />
      </Grid>
    </Grid>
    <Grid
      container
      spacing={2}
      alignItems="center"
      justifyContent="space-between"
    >
      <Grid item>
        <Grid container spacing={2} alignItems="center">
          <Grid item>
            <input
              type="image"
              src="https://uploads-ssl.webflow.com/622a433ae37287f9c4626c44/6230cd6da703392172e503e4_Roobet.svg"
              alt="roobet.com"
              onClick={() => (window.location.href = "https://roobet.com")}
            />
          </Grid>
          <Grid item sx={{ fontSize: "small" }}>
            © {new Date().getFullYear()} Roobet Casino. All rights reserved
          </Grid>
        </Grid>
      </Grid>
      <Grid item>
        {langValue != null && (
          <Select size="sm" value={langValue} onChange={putLang}>
            <Option value="en">
              <LangFlag langCode="gb" langValue="ENGLISH" />
            </Option>
            <Option value="es">
              <LangFlag langCode="es" langValue="ESPAÑOL" />
            </Option>
          </Select>
        )}
      </Grid>
      <Grid item>
        <img
          src="https://user-images.githubusercontent.com/33064544/199811356-1b2ec6e2-72ce-4fc4-9a52-c9001380a62e.png"
          alt="foot"
        />
      </Grid>
    </Grid>
  </>
);

export default Initial;
